.cover {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #f0f0f0;
  height: 100vh;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
}
.cover .cover-main {
    -ms-flex: 1;
    -webkit-box-flex: 1;
            flex: 1;
    margin: -20px 16px 0;
    text-align: center;
    z-index: 1;
}
.cover .cover-main p {
      line-height: 1.5rem;
      margin: 1em 0;
}
.cover .cover-main h1 {
      color: inherit;
      font-size: 37px;
      margin: .625rem 0 1rem;
      position: relative;
      text-align: center;
}
.cover .cover-main a {
      display: block;
      text-decoration: none;
}
.cover .cover-main a :hover {
        cursor: pointer;
}
.cover .cover-main .anchor {
      display: inline-block;
      text-decoration: none;
      -webkit-transition: all .3s;
      transition: all .3s;
      color: #35495e;
      font-size: 37px;
}
.cover .cover-main blockquote {
      text-align: center;
}
.cover .cover-main blockquote p strong {
        font-size: 22px;
        color: #35495e;
        font-weight: normal;
}
.cover .cover-main ul {
      line-height: 1.8;
      list-style-type: none;
      margin: 1em auto;
      max-width: 500px;
      padding: 0;
      overflow: hidden;
}
.cover .cover-main ul li {
        float: left;
        padding: 5px;
}
.cover .cover-main ul li a {
          cursor: pointer;
          display: block;
          -webkit-transition: color 1s;
          transition: color 1s;
}
.cover .cover-main ul li a:hover {
            color: #41b663;
}
.cover .cover-main ul li a:after {
            content: "\3001";
            height: 100%;
            width: 5px;
            line-height: 100%;
}
.cover .cover-main ul li sup {
          position: absolute;
          margin-top: -37px;
          width: 30px;
          height: 15px;
          background-image: url(data:image/gif;base64,R0lGODlhGwASAJEAAP////+9Y/+cAP///yH/C05FVFNDQVBFMi4wAwEAAAAh+QQJKAADACwAAAEAGwARAAACQJSPqcvtDxU4U4A7q6WyeqSF0vaB5DZmBvapmmmVnLuIKHwnNsut0Q8MCh+D4iFQTCqXzKQB2YxGBVCpVVm9SgsAIfkECSgAAwAsAAAAABsAEQAAAkCUj6nL7Q8VOFOAO6ulsnqkhdL2geQ2Zgb2qZpplZy7iCh8JzbLrdEPDAofg+IhUEwql8ykAdmMRgVQqVVZvUoLADs=);
          background-repeat: no-repeat;
}
.cover .cover-main .github {
      border: 1px solid var(--theme-color, #41b663);
      border-radius: 2rem;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      color: var(--theme-color, #41b663);
      display: inline-block;
      font-size: 1.05rem;
      letter-spacing: .1rem;
      margin: .5rem 1rem;
      padding: .75em 2rem;
      text-decoration: none;
      -webkit-transition: all .15s ease;
      transition: all .15s ease;
}
.cover .cover-main .getstart {
      border: 1px solid var(--theme-color, #41b663);
      border-radius: 2rem;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      background-color: var(--theme-color, #41b663);
      color: #fff;
      display: inline-block;
      font-size: 1.05rem;
      letter-spacing: .1rem;
      margin: .5rem 1rem;
      padding: .75em 2rem;
      text-decoration: none;
      -webkit-transition: all .15s ease;
      transition: all .15s ease;
}
.github-corner {
  border-bottom: 0;
  position: fixed;
  right: 0;
  text-decoration: none;
  top: 0;
  z-index: 1;
}
.github-corner a {
    display: block;
}
.github-corner a svg {
      color: #fff;
      fill: var(--theme-color, #41b663);
      height: 80px;
      width: 80px;
}
.github-corner a:hover svg .octo-arm {
      -webkit-animation: a .56s ease-in-out;
              animation: a .56s ease-in-out;
}
@-webkit-keyframes a {
0%, to {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
20%, 60% {
    -webkit-transform: rotate(-25deg);
            transform: rotate(-25deg);
}
40%, 80% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
}
}
@keyframes a {
0%, to {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
20%, 60% {
    -webkit-transform: rotate(-25deg);
            transform: rotate(-25deg);
}
40%, 80% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
}
}
